export const dynamic = 'force-dynamic';

import Link from 'next/link';
import { Plus, Edit, Eye } from 'lucide-react';
import Post from '@/models/Post';
import connectToDatabase from '@/lib/db';
import PostDeleteButton from '@/components/admin/PostDeleteButton';

async function getPosts() {
    await connectToDatabase();
    const posts = await Post.find({}).sort({ publishedAt: -1 });
    return posts;
}

export default async function AdminPosts() {
    const posts = await getPosts();

    return (
        <div>
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-display text-primary">Blog Posts</h1>
                <Link href="/admin/posts/new" className="flex items-center gap-2 bg-white text-black px-4 py-2 rounded font-semibold hover:bg-gray-200 transition-colors">
                    <Plus size={18} /> New Post
                </Link>
            </div>

            <div className="bg-surface border border-border rounded-lg overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-surfaceHighlight">
                        <tr>
                            <th className="p-4 text-sm font-medium text-secondary">Title</th>
                            <th className="p-4 text-sm font-medium text-secondary">Status</th>
                            <th className="p-4 text-sm font-medium text-secondary">Date</th>
                            <th className="p-4 text-sm font-medium text-secondary text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-border">
                        {posts.map((post: any) => (
                            <tr key={post._id} className="hover:bg-surfaceHighlight/50 transition-colors">
                                <td className="p-4">
                                    <div className="text-primary font-medium">{post.title}</div>
                                    <div className="text-xs text-secondary">/{post.slug}</div>
                                </td>
                                <td className="p-4">
                                    {post.published ? (
                                        <span className="px-2 py-1 rounded-full bg-green-500/10 text-green-500 text-xs border border-green-500/20">Published</span>
                                    ) : (
                                        <span className="px-2 py-1 rounded-full bg-yellow-500/10 text-yellow-500 text-xs border border-yellow-500/20">Draft</span>
                                    )}
                                </td>
                                <td className="p-4 text-secondary text-sm">
                                    {new Date(post.publishedAt).toLocaleDateString()}
                                </td>
                                <td className="p-4 text-right flex justify-end gap-3 items-center">
                                    <Link href={`/blog/${post.slug}`} target="_blank" className="text-secondary hover:text-white transition-colors" title="View Live">
                                        <Eye size={18} />
                                    </Link>
                                    <Link href={`/admin/posts/${post._id}`} className="text-accent hover:text-white transition-colors">
                                        <Edit size={18} />
                                    </Link>
                                    <PostDeleteButton id={post._id.toString()} />
                                </td>
                            </tr>
                        ))}
                        {posts.length === 0 && (
                            <tr>
                                <td colSpan={4} className="p-8 text-center text-secondary">No posts found.</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
